import React, { useState } from 'react';
import ProjectArticle from './ProjectArticle';
import styled from 'styled-components';

import {
  Flex,
  Spacer,
  VStack,
  Stack,
  Button,
  FormControl,
  FormLabel,
  Input,
  Checkbox,
  useToast,
} from '@chakra-ui/react';
import { useDispatch, useSelector } from 'react-redux';
import { setProjectList, selectProject } from '../features/projectsSlice';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const Dashboard = () => {
  const [projectInput, setProjectInput] = useState(false);
  const [currId, setId] = useState(0);

  const [title, setTitle] = useState('');
  const [hidden, setHidden] = useState(false);
  const [featured, setFeatured] = useState(false);

  const dispatch = useDispatch();
  const projects = useSelector(selectProject);

  const toast = useToast();
  const [innerProjects, setInnerProjects] = useState([]);

  const createProject = () => {
    toast({
      title: 'Project Created!',
      description: 'A new project has been created!',
      status: 'success',
      duration: 3000,
      isClosable: true,
    });

    setInnerProjects([
      ...innerProjects,
      {
        id: currId,
        name: title,
        featured: featured,
        project: 'programming',
        hidden: hidden,
      },
    ]);

    // dispatch(
    //   setProjectList([
    //     ...projects,
    //     {
    //       id: currId,
    //       name: title,
    //       featured: featured,
    //       project: 'programming',
    //       hidden: hidden,
    //     },
    //   ])
    // );
    setId(currId + 1);
  };

  const toggleProjectInput = () => {
    setProjectInput(!projectInput);
    setTitle('');
    setHidden(false);
    setFeatured(false);
  };
  const onDragEnd = event => {
    // console.log(event.destination.index + ' ' + event.source.index);

    if (event.destination == null || event.source == null) {
      return;
    }
    var destination = event.destination.index,
      source = event.source.index;

    var startSlice = innerProjects.slice(0, Math.min(destination, source));
    var endSlice = innerProjects.slice(
      Math.max(destination, source) + 1,
      innerProjects.length
    );

    if (destination > source) {
      var changedSlice = innerProjects.slice(source + 1, destination + 1);
      var editedArray = [
        ...startSlice,
        ...changedSlice,
        innerProjects[source],
        ...endSlice,
      ];
    } else {
      var changedSlice = innerProjects.slice(destination, source);
      var editedArray = [
        ...startSlice,
        innerProjects[source],
        ...changedSlice,
        ...endSlice,
      ];
    }

    console.log(startSlice);
    console.log(changedSlice);
    console.log(endSlice);
    setInnerProjects(editedArray);
    // dispatch(setProjectList(editedArray));
  };
  return (
    <DashboardContainer>
      <Wrapper>
        <Flex align="center" p={5}>
          <Title>
            <span>Project Dashboard</span>
          </Title>
          <Spacer />
          <Button colorScheme="green" size="sm" onClick={toggleProjectInput}>
            {projectInput ? <span>Close</span> : <span>Add Project </span>}
          </Button>
        </Flex>

        {projectInput && (
          <InputWrapper>
            <FormControl>
              <Stack mb={'5'} direction="column">
                <FormLabel mr={'auto'}> Title </FormLabel>
                <Input
                  type="text"
                  onBlur={event => setTitle(event.currentTarget.value)}
                />
                <Flex>
                  <Checkbox onChange={() => setHidden(!hidden)}>
                    {' '}
                    Hidden{' '}
                  </Checkbox>
                  <Checkbox ml={4} onChange={() => setFeatured(!featured)}>
                    {' '}
                    Featured{' '}
                  </Checkbox>
                  <Spacer />
                  <Button colorScheme="yellow" onClick={createProject}>
                    {' '}
                    Add Project!
                  </Button>
                </Flex>
              </Stack>
            </FormControl>
          </InputWrapper>
        )}
      </Wrapper>

      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="hola">
          {provided => (
            <Articles ref={provided.innerRef} {...provided.droppableProps}>
              {innerProjects.length > 0 ? (
                innerProjects.map((project, index) => {
                  return (
                    <Draggable
                      draggableId={'d' + project.id}
                      index={index}
                      key={project.id}
                    >
                      {(provided, snapshot) => (
                        <DraggableDiv
                          {...provided.draggableProps}
                          ref={provided.innerRef}
                        >
                          <ProjectArticle
                            id={project.id}
                            title={project.name}
                            featured={project.featured}
                            project={project.project}
                            hidden={project.hidden}
                            isDragging={snapshot.isDragging}
                            provided={provided}
                          />
                        </DraggableDiv>
                      )}
                    </Draggable>
                  );
                })
              ) : (
                <p> Nothing to show!</p>
              )}
              {provided.placeholder}
            </Articles>
          )}
        </Droppable>
      </DragDropContext>
    </DashboardContainer>
  );
};

const DashboardContainer = styled.div`
  color: black;
  background-color: white;
  height: 1000vh;
`;
const DraggableDiv = styled.div`
  margin-bottom: 10px;
`;
const Title = styled.h1`
  span {
    font-size: 30px;
    font-weight: bold;
  }
`;

const InputWrapper = styled.div`
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding: 20px;
  margin-bottom: 20px;
`;
const Articles = styled.div`
  margin: auto;
  border-radius: 4px;
  padding: 30px 15px;
  width: 90%;
  border: 2px solid rgba(0, 0, 0, 0.1);
`;
const Wrapper = styled.div`
  width: 80%;
  margin: auto;
`;
export default Dashboard;
