import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import {
  Badge,
  Button,
  Checkbox,
  Radio,
  RadioGroup,
  AlertDialog,
  AlertDialogBody,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogContent,
  AlertDialogOverlay,
  useToast,
} from '@chakra-ui/react';
import { useDispatch, useSelector } from 'react-redux';
import { setProjectList, selectProject } from '../features/projectsSlice';
import MenuIcon from '@material-ui/icons/Menu';
// eslint-disable-next-line react/prop-types
const ProjectArticle = ({
  id,
  title,
  featured,
  project,
  hidden,
  isDragging,
  provided,
}) => {
  const [isFeatured, setFeatured] = useState(false);
  const [projectType, setProjectType] = useState('programming');
  const [isDeleteOpen, setIsDeleteOpen] = useState(false);
  const [isHidden, setHidden] = useState(false);
  const onClose = () => setIsDeleteOpen(false);
  const cancelRef = React.useRef();

  const toast = useToast();

  const dispatch = useDispatch();
  const projects = useSelector(selectProject);
  const deleteItem = () => {
    toast({
      title: 'Project Deleted',
      description: 'The project has been deleted!',
      status: 'success',
      duration: 3000,
      isClosable: true,
    });
    onClose();
    var filteredProjects = projects.filter(project => {
      // eslint-disable-next-line react/prop-types
      return project.id !== id;
    });
    dispatch(setProjectList(filteredProjects));
  };

  useEffect(() => {
    setFeatured(featured);
    setProjectType(project);
    setHidden(hidden);
  }, []);

  return (
    <Container isDragging={isDragging}>
      <MainContent>
        <div {...provided.dragHandleProps}>
          <Burger />
        </div>
        <Title> {title}</Title>
        {isFeatured && <Badge colorScheme="yellow"> Featured </Badge>}
        {isHidden && <Badge colorScheme="red"> Hidden </Badge>}
        {projectType == 'programming' ? (
          <Badge colorScheme="blue"> programming </Badge>
        ) : (
          <Badge colorScheme="green"> Video Editing </Badge>
        )}
      </MainContent>
      <Form>
        <SRadioGroup onChange={setProjectType} value={projectType} size="sm">
          <SRadio value="programming"> P</SRadio>
          <SRadio value="editing"> V </SRadio>
        </SRadioGroup>
        <FeaturedCheckbox
          isChecked={isFeatured ? true : false}
          onChange={() => setFeatured(!isFeatured)}
          size="sm"
        >
          Featured
        </FeaturedCheckbox>

        <FeaturedCheckbox
          isChecked={isHidden ? true : false}
          onChange={() => setHidden(!isHidden)}
          size="sm"
        >
          Hidden
        </FeaturedCheckbox>
        <EditButton colorScheme="blue" size="sm">
          {' '}
          Edit{' '}
        </EditButton>
        <DeleteButton
          colorScheme="red"
          size="sm"
          onClick={() => setIsDeleteOpen(true)}
        >
          Delete
        </DeleteButton>
        <AlertDialog
          isOpen={isDeleteOpen}
          leastDestructiveRed={cancelRef}
          onClose={onClose}
        >
          <AlertDialogOverlay>
            <AlertDialogContent style={{ color: 'black' }}>
              <AlertDialogHeader fontSize="lg" fontWeight="bold">
                Delete Project
              </AlertDialogHeader>
              <AlertDialogBody>
                Are you sure you want to delete this project?
              </AlertDialogBody>
              <AlertDialogFooter>
                <Button ref={cancelRef} onClick={onClose}>
                  Cancel
                </Button>
                <Button colorScheme="red" onClick={deleteItem} ml={3}>
                  Delete
                </Button>
              </AlertDialogFooter>
            </AlertDialogContent>
          </AlertDialogOverlay>
        </AlertDialog>
      </Form>
    </Container>
  );
};

const Container = styled.div`
  background: ${props => (props.isDragging ? 'rgb(244,244,244)' : 'white')};
  border: 1px solid rgba(128, 128, 128, 0.3);
  border-radius: 5px;
  display: flex;
  align-items: center;
  width: 1100px;
  padding: 10px 0;
  span {
    margin: 0 10px;
  }
`;

const Burger = styled(MenuIcon)`
  margin: 0 20px;
`;
const MainContent = styled.div`
  min-width: 450px;
  display: flex;
  align-items: center;
`;

const Form = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const Title = styled.h1`
  font-size: 15px;
  font-weight: bold;
  margin-right: 10px;
`;

const EditButton = styled(Button)`
  margin: 0 20px;
`;
const FeaturedCheckbox = styled(Checkbox)`
  margin-left: 20px;
`;

const SRadioGroup = styled(RadioGroup)`
  margin-left: auto;
  font-size: 15px;
`;

const SRadio = styled(Radio)`
  font-size: 5px;
`;

const DeleteButton = styled(Button)`
  margin-right: 20px;
`;

export default ProjectArticle;
